/*RESET*/
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/*GENERAL*/
.card__wrapper {
  padding-inline: 50px;
  background-color: #a7a1bd;
}

.card {
  max-width: 550px;
  margin: 80px auto;
  border-radius: 15px;
  font-size: 2rem;
  background-color: whitesmoke;
  transition: all 0.2s ease;
}

.card:hover {
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.15);
  transform: translateY(-4px);
}

.card__header {
  position: relative;
  height: 42vh;
  border-radius: 15px 15px 0 0;
  background-color: #bdc2a9;
}

.card__tag {
  position: absolute;
  top: 15px;
  left: -13px;
  display: inline-block;
  border-radius: 10px 50px 50px 10px;
  padding: 15px 30px 15px 15px;
  font-size: 0.8em;
  font-weight: 600;
  background-color: #d2ca2d;
}

.card__content {
  padding: 40px 30px;
}

.card__title {
  margin-bottom: 20px;
  font-size: 1.2em;
  font-weight: 500;
}

.sub-title {
  font-size: 0.6em;
  font-weight: 300;
  color: #666;
  white-space: nowrap;
}

.card__text {
  font-size: 0.8em;
}